/*galler welcome*/
.gallery-welcome {
  position: relative;
  background: var(--sub-page-background);
}
.gallery-welcome-img {
  width:100%;
  height:350px;
  object-fit: cover;
  object-position:10% 10%;
  overflow: hidden;
}
.gallery-welcome-content-wrapper {
  position: absolute;
  left:0;
  top:0;
  width:100%;
  height: 100%;
  background-image: var(--sub-page-gradient);
  display: flex;
  justify-content: center;
  align-items: center;
}
.gallery-welcome-content {
  padding:12px;
}
.gallery-welcome-title-wrapper,.gallery-welcome-text-wrapper {
  overflow: hidden;
}
.gallery-welcome-title {
  font-size: 3.6rem;
  color: #fff;
  font-weight: 700;
  text-align: center;
  margin:5px 2px;
  letter-spacing: 1px;
  animation:gallerytitle .8s ease;
}
@keyframes gallerytitle {
  0% {
    transform: translateX(-200%);
  }
  100% {
    transform: translateX(0);
  }
}
.gallery-welcome-text {
  color:#eee;
  text-align: center;
  font-weight: 550;
  font-size: 1.1rem;
  animation: gallerytext .8s ease;
}
@keyframes gallerytext {
  0% {
    transform: translateX(200%);
  }
  100% {
    transform: translateX(0);
  }
}
