/*galler-wrapper*/
.galler-wrapper {
  background:#ddd;
  padding: 10px;
}
.gallery-flex {
  background:#fff;
  width:96%;
  margin:10px auto;
  max-width: 800px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding: .7rem .5rem;
}
.gallery-card{
  margin:8px;
  flex:1 1 150px;
  max-width: 250px;
  position: relative;
  overflow: hidden;
  border-radius:1px;
}
.gallery-overlay {
  visibility: hidden;
  position: absolute;
  letf:0;
  top:0;
  width: 100%;
  height: 99.2%;
  background: rgba(0,0,0,.3);
  color:#fff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .1s ease;
}
.gallery-overlay-icon-wrapper {
  border-radius:50%;
  width:32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  outline-offset: 5px;
  outline: 2px dashed #fff;
  transition: all .1s ease;
}
.gallery-overlay-icon {
  width:30px;
  height: 30px;
  fill: #fff;
  transition: all .15s ease;
  padding: 8px;
}
.gallery-overlay-icon-wrapper:hover {
  outline: 2px dashed var(--color-primary);
}
.gallery-overlay-icon:hover {
  fill:var(--color-primary);
  transform: scale(1.2);
}
.gallery-card:hover .gallery-overlay {
  visibility: visible;
}
.gallery-card:hover .gallery-card-img {
   transform: scale(1.3) rotate(10deg);
}

.gallery-card-img {
  width:100%;
  max-height:150px;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
  filter:saturate(115%);
  border-radius: 1px;
  box-shadow: 0 2px 10px #eee;
  transition: all .65s ease;
}

/*gallery popup */
.gallery-popup-wrapper {
  z-index: 40;
  position:fixed;
  left:0 ;
  top:0;
  width:100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0,.78);
  animation: popupwrapperanimation .1s ease;
}
@keyframes popupwrapperanimation {
  0% {
    opacity: 0;
  }
  100% {
   opacity:1;
  }
}

.gallery-popup {
  padding: .2rem .3rem;
  margin: 0 auto;
  width: 90%;
  max-width:700px;
  animation: popupanimation .2s ease;
}
@keyframes test2  {
  0% {
    visibility: hidden;
    transform: scale(0.7);
  }
  100% {
    visibility: visible;
    transform: scale(1);
  }
}
.gallery-popup-killer {
  margin:.2rem auto 1rem auto;
  display: flex;
}
.gallery-popup-killer-icon {
  width:30px;
  height: 30px;
  fill:#fff;
  outline-offset: 6px;
  outline: 2px solid #fff;
  margin:.1rem .6rem .3rem auto;
  border-radius:50%;
  transition: all .15s ease;
}
.gallery-popup-killer-icon:focus {
  transform: scale(0.95);
}
.gallery-popup-img {
  width:100%;
  border-radius:3px;
  filter:saturate(120%);
}



/*album*/
.album-wrapper {
  margin:.5rem;
}
.album {
  width:98%;
  max-width:800px;
  margin: .5rem auto;
  padding: .4rem .3rem;
  display: flex;
  flex-flow: row wrap;
}
.album-title {
  width:100%;
  text-indent:.5rem;
  font-size:2rem;
  font-weight:700;
  color:var(--color-charcoal);
}

.album-card {
  flex:1 1 250px;
  max-width: 420px;
  margin:.3rem;
  position: relative;
  border-radius:3px;
  box-shadow: 0 0 3px #eee;
}
.album-card-img {
  width:100%;
  height: 170px;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
  filter:saturate(118%);
  border-radius:3px;
}

.album-card-overlay {
  position: absolute;
  top:0;
  left:0;
  width:100%;
  height:98%;
  display: flex;
  align-items: flex-end;
  background: rgba(0,0,0,.28);
  color:#fff;
  border-radius: 3px;
}
.album-card-overlay-content {
  margin:.1rem .1rem .8rem .4rem;
}
.album-card-overlay-title {
  font-size: 1.6rem;
  font-weight:600;
}
.album-card-overlay-text {
  margin:.2rem;
}
/*album gallery some*/
.album-gallery-topic {
  width:98%;
  margin:.4rem auto;
  max-width: 800px;
  padding: .5rem .3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.album-gallery-title {
  flex:1;
  font-size: 2.2rem;
  text-indent: .5rem;
  font-weight:900;
  color:var(--color-primary);
}
.album-gallery-back-wrapper {
  flex:1;
  display: flex;
  justify-content: flex-end;
}
.album-gallery-back {
  color:var(--color-primary);
  text-decoration: none;
  font-weight:500;
  font-size:1.05rem;
  display: inline-block;
  border:2px solid #eee;
  border-radius:3px;
  padding: .3rem .4rem;
}





