/*blog wrapper*/
.blog-wrapper {
  background: var(--color-background);
}
.blog-flex {
  width:96%;
  margin:10px auto;
  max-width:700px;
  padding: 12px 10px;
  display: flex;
  flex-flow:row wrap ;
}

.blog-card {
  flex:1 1 250px;
  max-width:350px;
  margin:10px 8px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 0 4px #ddd;
  border:1px solid #ddd;
}

.blog-card-img {
  width:100%;
  height: 150px;
  border-radius: 4px 4px 0 0;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
  
}
.blog-card-content {
  padding:.4rem .2rem;
  
}
