  font-weight: 600;
}
.blog-card-description {
  color: grey;
  font-size: .9rem;
  width:100%;
  padding:.7rem .3rem;
}
.blog-card-link {
  text-decoration: none;
  display: inline-block;
  margin:10px 5px 5px 12px;
  color: var(--color-primary);
  font-size: 1rem;
  transition: all .2s ease;
}


/*blog detail*/

.blog-detail-wrapper {
  padding: 10px 5px;
