}
.blog-detail-content {
  margin:2px 0 5px 0;
  padding: 8px 5px;
  width:100%;
}
.blog-detail-date-icon {
  width: 20px;
  height: 20px;
  margin:0 5px;
  fill:var(--color-primary);
  transform: translateY(4px);
}
.blog-detail-date {
  width: 100%;
  text-align: right;
  margin:5px 0;
  padding: 3px 10px 3px 3px ;
}
.blog-detail-title {
  text-indent: .1rem;
  font-size: 1.3rem;
  color: var(--color-charcoal);
  font-weight: 600;
  width:100%;
}
.blog-detail-description {
  width:100%;
