/*_________*/
html {
  font-family: "Lato",Sans-Serif;
}
:root {
  margin:0;
  padding: 0;
  --color-background:#fff;
  --height-xl:450px;
  --height-lg:450px;
  --height-md:420px;
  --height-sm:350px;
  --color-primary:#0047Ab;
  --color-secondary:crimson;
  --color-menuhover:#eee;
  --color-boxshadow:#eee;
  --color-333:#333;
  --color-icon:black;
  --color-orange:orange;
  --color-charcoal:#36454F;
  --color-title:#28282B;
  --color-green:#00A36C;
  --footer-wrapper:800px;
  --footer-box:300px;
  --footer-icon-color:#ddd;
  --home-page-gradient:linear-gradient(45deg,rgba(0,0,0,.6) 25%,rgba(0,0,0,.15) 100%);
  --home-page-background:#0047Ab;
  --sub-page-gradient:linear-gradient(rgba(0,0,0,.3),rgba(0,0,0,.3));
  --sub-page-background:#0047Ab;
}
.links {
  text-decoration: none;
  color:#333;
  transition: background 1s ease;
}
.mail-link {
  text-decoration: none;
  color:var(--color-primary);
}

.links:hover {
  
}
.links:active {
  
}
/*Nav bar*/
.navbar {
  display: flex;
  justify-content: space-around;
  box-shadow: 0 1px 5px #eee;
  padding:.7rem .4rem;
}

.logo-wrapper {
  display: flex;
  justify-content: center;
  align-self: center;
  padding: 2px;
  flex:1;
}
.logo {
  border-radius:50%;
  width:3rem;
}
.logo-text-wrapper {
  display: flex;
  justify-content:center;
  align-items: center;
}
.logo-text {
  font-size: 2rem;
  font-weight: 700;
  color:var(--color-charcoal);
}


.menu-items-wrapper {
  display:flex;
  flex:2;
  justify-content: space-around;
  align-items: center;
  padding: 1px 2px 1px 5px;
}
.one-menu-item-wrapper {
  position: relative;
}
.icon {
  width:20px;
  height:20px;
  fill:var(--color-333);
}
.arrow-down-icon {
  width:1rem;
  height: .6rem;
  transition: transform .2s ease;
}
.arrow-down-open {
  transform: rotate(-90deg);
  animation:transform .2s ease;
}
.menu-item {
  display: flex;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  color:#333;
  padding:10px;
  flex:1 1 2.5rem;
  cursor: pointer;
}
.menu-item-text {
  margin:1px 2px 1px 4px;
  font-weight:500;
  font-size: 1.05em;
}

.navbar-button {
  padding:.65rem 1.25rem;
  border:none;
  outline: none;
  background:var(--color-333) ;
  color:#fff;
  box-shadow: 1px 3px 8px #ddd;
  font-size: 1rem;
  transition: all .08s ease;
  border-radius: 2px;
  margin:1px 5px 1px 1px;
  font-weight:500;
}
.navbar-button:hover {
  opacity: .95;
}
.navbar-button:focus {
  background: var(--color-primary);
}
.drop-down {
  position:absolute;
  top:calc(100%);
  left:10%;
  min-width:195px;
  padding:5px;
  box-shadow: 0 5px 10px rgba(0,0,0,.8);
  border-radius: 2px;
  transition: all .1s ease;
  opacity: 0;
  z-index:20;
  background: #fff;
  color: #333;
}
.drop-down-invisible {
  visibility: hidden;
}
.animate-drop-down {
  visibility: visible;
  opacity: 1;
  transform: translateY(2px);
  transition: all .05s ease;
  animation: bigscreendropdownanimate .3s ease;
}
@keyframes bigscreendropdownanimate{
  0% {
    transform: translateY(-10%);
  }
  100% {
    transform: translateY(0);
  }
}
.drop-down-link {
  display: flex;
  text-decoration: none;
  align-items: center;
  color:#333;
  flex:1 1 2.5rem;
  padding:.5rem .05rem;
  width:100%;
 transition: all .2s all;
  margin:2px auto;
}
.drop-down-link-text {
   display: inline-block;
   margin:2px 2px 2px 8px;
}
.drop-down-link:hover {
   opacity: .9;
}
.drop-down-link:focus {
  background: #ddd;
}


/*small Scrren Nav bar*/

.sm-navbar-wrapper {
  display: none;
}
.sm-navbar {
  position:relative;
}
.sm-navbar-menu {
  position: absolute;
  top:calc(100% + .3rem);
  right:50%;
  padding: .8rem .5rem;
  width:300px;
  z-index:10;
  background: #333;
  color:#fff;
  box-shadow:0 3px 10px rgba(0,0,0,.9);
  transition: all .3s ease;
  border-radius:3px;
  
}
.sm-navbar-menu-animate {
  visibility: visible;
  opacity: 1;
  transition: all .05s ease;
  animation: smscreendropdownanimate .3s ease;
}
@keyframes smscreendropdownanimate{
  0% {
    opacity: 0;
    transform: translateX(10px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.sm-navbar-button {
  padding: .7rem 1.3em;
  background: var(--color-333);
  border: 1px solid var(--color-333);
  color: #fff;
  font-weight: 600;
  border-radius: 2px;
 transition: all .08s ease;
 box-shadow: 1px 3px 8px #ddd;
}

.sm-navbar-button:hover {
  opacity: .95;
}
.sm-navbar-button:focus {
  background: var(--color-primary);
 border: 1px solid var(--color-primary);
}
.sm-navbar-dropper {
  display: flex;
  justify-content:space-between;
  align-self: center;
  padding: 7px 2px;
  overflow: hidden;
  color:#fff;
  background: #333;
  color: #fff;
}
.sm-navbar-dropper:focus {
  
}
.sm-navbar-link {
  display: flex;
  width:100%;
  padding: .5rem .3rem .5rem .5rem;
  text-decoration: none;
  color:#fff;  
}
.sm-navbar-link:hover {
}
.sm-navbar-link:focus {
}
.sm-navbar-text {
  padding:0 0 0 6px;
}

.sm-navbar-droping-wrapper {
  display: flex;
  flex-flow: column nowrap;
  width:100%;
  padding: .2rem .2rem .2rem .5rem;
}
.sm-drop-down-animate {
  animation:dropdownslide.4s ease;
}
@keyframes dropdownslide {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}



.menu-icon {
  width:2.3rem;
  transition: all .1s ease;
  padding: 1px;
}
.menu-icon:hover {
  opacity: .9;
}




/*welcome theme*/
.welcome-wrapper {
  width:100%;
  position: relative;
  background: var(--color-primary);
  overflow: hidden;
}

.welcome-img {
  display: block;
  width:100vw;
  max-height: 400px;
  overflow: hidden;
  object-fit: cover;
  object-position:center;
}
.welcome-content-wrapper {
  position: absolute;
  top:0;
  left:0;
  bottom:0;
  width:100%;
  color:#fff;
  background-image: linear-gradient(45deg,rgba(0,0,50,.03) 15%,rgba(0,0,0,.01) 100%);
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  padding: 2px;
}
.welcome-title {
  font-family: Oswald,Sans-Serif;
  font-size: 3rem;
  font-weight: 900;
  flex:1;
  text-align: center;
}
.welcome-info {
  flex:1;
  font-weight: 500;
}
.inspired-by {
  font-size: 2rem;
}
.verse {
  font-size: 1.2rem;
}
.quote {
  font-size: 3rem;
  display: inline;
  font-weight:900;
  font-family: serif;
}

/*welcome 1 (home page"*/
.home-page {
  width:100%;
  position: relative;
  background: var(--sub-page-background);
  padding: 0;
}
.home-page-img {
  width:100%;
  height:var(--height-xl);
  max-height: 100vh;
  overflow: hidden;
  object-fit: cover;
  object-position:center;
  filter:saturate(115%);
}
.home-page-overlay {
  position: absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background-image: var(--home-page-gradient);
  color:#fff;
  display:flex;
  align-items: center;
  justify-content: center;
}
.home-page-overlay-content {
  flex:1 1 300px;
  max-width:800px;
  padding:15px 10px;
  margin:2px 2px 2px 10px;
}
.home-page-title-wrapper {
  overflow: hidden;
}
.home-page-title {
  font-size: 3rem;
  font-weight:900;
  transition: animation 3s ease;
  animation: homepagetitle .9s ease;
}
.home-page-title-itself {
  display: block;
}
.home-page-description-wrapper {
  overflow: hidden;
}
.home-page-description {
  text-indent: 5px;
  font-size: 1.1rem;
  margin:5px;
  font-weight:700;
  animation: homepagedescription 1.1s ease;
}
.home-page-button-wrapper {
  margin:18px 5px 5px 5px ;
  padding: 10px;
  overflow: hidden;
}
.home-page-first-button {
  padding: 1rem 1.1rem;
  font-size: 1.1rem;
  margin:5px 10px;
  font-weight: 800;
  border-radius:3px;
  background: #fff;
  color:#333;
  border:2px solid #fff;
  transition: all .1s ease;
  animation: buttonanimation .7s ease;
}
.home-page-first-button:focus {
  background: #fff;
  color:var(--color-333);
  border:2px solid #fff;
}
.home-page-second-button {
  padding: 1rem 1.1rem;
  font-size: 1.1rem;
  margin:5px 10px;
  background: var(--color-primary);
  color:#fff;
  font-weight: 800;
  border: 2px solid var(--color-primary);
  border-radius:3px;
  transition: all .1s ease;
  animation:  buttonanimation 1s ease;
}
.home-page-second-button:focus {
  background: var(--color-333);
  border: 2px solid var(--color-333);
}




@keyframes homepagetitle {
  0% {
    transform: translateY(-200%);
  }
  100% {
    transform:translateY(0);
  }
}
@keyframes homepagedescription {
  0% {
    transform: translateX(-200%);
  }
  100% {
    transform:translateX(0);
  }
}

@keyframes buttonanimation {
  0% {
    transform: translateY(200%);
  }
  100% {
    transform:translateY(0);
  }
}








