/*iframe */
.iframe-wrapper {
  position: relative;
  
}
.iframe-img {
  width:100%;
  min-height:220px;
  max-height:340px ;
  object-fit: cover;
  object-position:0 56%;
  overflow: hidden;
}
.iframe-overlay {
  position:absolute;
  left:0;
  top:0;
  width:100%;
  height: 100%;
  background: linear-gradient(rgba(0,0,0,.5),rgba(0,0,0,.4));
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.iframe-overlay-content {
}
.iframe-overlay-title {
  font-size: 2.7rem;
  text-align: center;
  font-weight: 800;
  
}
.iframe-overlay-icon {
  display: block;
  margin:10px auto;
  width:80px;
  height: 80px;
  border-radius:50%;
  fill:var(--color-primary);
  opacity: .9;
  outline-offset: 2px;
  outline: 2px solid grey;
  transition: all .1s ease;
}
.iframe-overlay-icon:hover {
  opacity: .85;
  outline-offset: 3px;
}

/*iframe popup*/
.iframe-video-wrapper {
  position: fixed;
  top:0;
  left:0;
  width:100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  background: rgba(0,0,0,.7);
}
.iframe-video-content {
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-content: center;
  flex:1;
  max-width: 800px;
}
.iframe-video-wrapper-animate {
  animation: iframepopup .3s ease;
}
@keyframes iframepopup{
  0% {
    opacity: 0;
    transform: scale(.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.iframe-close-icon {
  width:35px;
  height:35px;
  fill:#fff;
  outline-offset: 2px;
  outline: 1px solid #fff;
  margin:.1rem .6rem .7rem auto;
  border-radius:50%;
  transition: all .15s ease;
}
.iframe-video {
  width:95%;
  margin:.4rem auto;
